.logo {
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    display: block;
    box-sizing: border-box;
  }

  @include hover-focus {
    filter: drop-shadow(0 0 5px var(--color-pacific-blue));
    z-index: 2;
  }

  &:active {
    opacity: 0.4;
  }

  @include state-link;
  @include noselect;
}
